/*==================================================
  FAQ 전체 페이지 스타일
==================================================*/
.faq-page {
    background-color: #000; /* 검정 배경 */
    color: #fff;           /* 흰색 텍스트 */
    font-family: 'Pretendard', sans-serif;
    min-height: 50vh;
    margin: 0;
    padding: 0;
}

/*==================================================
  헤더(Header) 영역
==================================================*/
.faq-header {
    margin-top: 200px;
    text-align: center;
    padding: 4rem 1rem 3rem;
}

.faq-title {
    font-size: 4.5rem;
    font-weight: 800;
    margin: 0;
    letter-spacing: -1px;
}

.faq-subtitle {
    font-size: 2rem;
    font-weight: 300;
    color: #ccc;
    margin: 1rem 0 0;
}

.faq-subtitle strong {
    color: #fff;
    font-weight: 700;
}

/*==================================================
  FAQ 탭 UI 스타일
==================================================*/
.faq-tabs {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin: 2rem 0 1rem;
}

.faq-tab {
    background: transparent;
    border: 2px solid #333;
    border-radius: 4px;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    color: #ccc;
    cursor: pointer;
    transition: background 0.2s ease, color 0.2s ease;
}

.faq-tab:hover {
    background: rgba(255, 255, 255, 0.08);
}

.faq-tab.active {
    background: #ffdc00;
    color: #000;
    border-color: #ffdc00;
}

/* 탭이 많아질 경우 스크롤바 없이 눕혀서 정렬 */
@media (max-width: 480px) {
    .faq-tabs {
        overflow-x: auto;
        padding: 0 1rem;
    }
    .faq-tab {
        flex-shrink: 0;
    }
}

/*==================================================
  FAQ 검색창 스타일
==================================================*/
.faq-search-wrapper {
    display: flex;
    justify-content: center;
    margin: 1.5rem 0 0.5rem;
}

.faq-search-input {
    width: 100%;
    max-width: 600px;
    padding: 0.6rem 1rem;
    border: 1px solid #444;
    border-radius: 4px;
    background-color: #111;
    color: #fff;
    font-size: 1rem;
    transition: border-color 0.2s ease;
}

.faq-search-input::placeholder {
    color: #777;
}

.faq-search-input:focus {
    outline: none;
    border-color: #ffdc00;
}

.faq-no-results {
    text-align: center;
    color: #ccc;
    font-size: 1.2rem;
    margin: 2rem 0;
}

/*==================================================
  FAQ 아이템 컨테이너
==================================================*/
.faq-container {
    max-width: 1000px;
    margin: 0 auto;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

/*==================================================
  개별 FAQ 아이템
==================================================*/
.faq-item {
    background-color: #111; /* 좀 더 진한 배경 */
    border: 1px solid #333;
    border-radius: 8px;
    overflow: hidden;
    transition: all 0.3s ease;
}

/*==================================================
  질문(함수 시그니처) 부분: 인텔리제이 코드 접기 스타일
==================================================*/
.faq-signature-line {
    cursor: pointer;
    display: flex;
    align-items: center;
    font-family: 'Fira Code', monospace; /* 코드 느낌 폰트 */
    font-size: 0.95rem;
    background: #1f2a44;
    padding: 0.75rem 1rem;
    user-select: none;
    position: relative;
}

/* folded 주석(접힘 표시) */
.faq-modifier {
    color: #6b9972;  /* IntelliJ 내 주석 스타일을 모사한 색상 */
    margin-right: 8px;
}

/* 질문 앞에 붙는 아이콘 */
.faq-icon {
    margin-right: 0.5rem;
    font-size: 1.1rem;
    color: #ffdc00;    /* 노란색 포인트 */
}

/* 코드 텍스트 */
.faq-code-block {
    color: #ffdc00; /* IntelliJ에서 키워드/함수명 강조 느낌 */
    margin-right: auto;
}

/* 접기/펼치기 아이콘 */
.faq-fold-icon {
    color: #999;
    margin-left: 6px;
    margin-right: 4px;
    font-size: 0.9rem;
    transition: transform 0.2s ease; /* 부드러운 회전 애니메이션 */
    line-height: 1;
}

/* 닫힌 중괄호 */
.faq-brace-close {
    color: #ffdc00;
}

/*==================================================
  질문 바로 아래: 간략 요약(summary)
==================================================*/
.faq-summary {
    padding: 0.25rem 1rem;  /* 접힌 상태 요약 */
    font-size: 0.9rem;
    color: #ccc;
    font-style: italic;
}

/* 펼친 상태에서 FAQ 내부 요약 styling 별도 지정 (예: .faq-answer-block .faq-summary) */
.faq-answer-block .faq-summary {
    padding: 0;              /* 너무 띄워져 있지 않도록 */
    font-size: 0.85rem;
    color: #aaa;
    font-style: italic;
}

@media (max-width: 480px) {
    .faq-summary {
        padding-left: 2rem;
        font-size: 0.85rem;
    }
}

/*==================================================
  답변(펼쳐진 상태) 영역
==================================================*/
.faq-answer-block {
    background-color: #1f2a44;
    padding: 0.5rem 1rem;
    animation: fadeIn 0.3s ease forwards;
}

.faq-answer-block pre {
    white-space: pre-wrap;
    line-height: 1.4;
    color: #ddd; /* 코드 내용 색 */
    font-family: 'Fira Code', monospace;
    font-size: 0.85rem;
    margin: 0;
}

/*==================================================
  답변 내부 핵심 키워드 강조
==================================================*/
.faq-answer-block .faq-highlight {
    background-color: rgba(255, 220, 0, 0.2); /* 연한 노란 배경 */
    font-weight: 600;
    color: #ffdc00;
    padding: 0.1rem 0.2rem;
    border-radius: 4px;
}

/*==================================================
  새로 추가된 CTA 섹션 스타일
==================================================*/
.faq-cta-section {
    text-align: center;
    margin: 6rem auto 6rem;
    max-width: 800px;
    padding: 0 1rem;
}

.cta-info {
    font-size: 1rem;
    color: #aaa;
    margin: 0 0 1rem;
}

.cta-question {
    font-size: 2rem;
    font-weight: 700;
    color: #fff;
    margin: 0 0 2rem;
}

.cta-button-group {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
}

@media (min-width: 600px) {
    .cta-button-group {
        flex-direction: row;
        justify-content: center;
    }
}

.cta-button {
    border: none;
    border-radius: 30px;
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.3s ease;
    color: #000; /* 버튼 배경에 대비되는 컬러 */
    min-width: 180px;
    font-family: 'Pretendard', sans-serif;
}

/* 카카오톡 버튼: 노란색 */
.cta-button.kakao {
    background-color: #ffdc00;
}

.cta-button.kakao:hover {
    background-color: #ffd700;
}

/* 커피챗 버튼: 연한 갈색 혹은 다른 색상 */
.cta-button.coffee {
    background-color: #cfa17f; /* 원하시는 색상으로 조정 */
}

.cta-button.coffee:hover {
    background-color: #b4896b;
}

/*==================================================
  등장 애니메이션
==================================================*/
@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(-4px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

/*==================================================
  반응형(Responsive) 설정
==================================================*/
@media (max-width: 768px) {
    /* 모바일에서 탭과 검색창 여백 간격 조정 */
    .faq-tabs {
        margin: 1.5rem 0 1rem;
    }
    .faq-search-wrapper {
        margin: 1rem 0 0.5rem;
    }
}

/*==================================================
  접근성 모션 감소(애니메이션 비활성화)
==================================================*/
@media (prefers-reduced-motion: reduce) {
    .faq-answer-block {
        animation: none;
    }
}
